<template>
  <v-container class="bodyContent">
    <v-dialog v-model="modal_successRegis" max-width="350">
      <v-card>
        <v-card-title class="text_question">สมัครสมาชิกสำเร็จ</v-card-title>

        <v-card-text>
          กรุณายืนยันอีเมลเพื่อเริ่มการใช้งานระบบตาไว
          ขอบคุณที่ร่วมเป็นส่วนหนึ่งกับเรา
        </v-card-text>

        <v-card-actions>
          <v-btn
            color="green darken-1"
            text
            block
            to="/login"
            @click="modal_successRegis = false"
          >
            ตกลง
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="modal_failRegis" max-width="350">
      <v-card>
        <v-card-title class="text_question">เกิดข้อผิดพลาด</v-card-title>

        <v-card-text>
          ไม่สามารถดำเนินการสมัครสมาชิกได้ในขณะนี้
          กรุณาติดต่อเจ้าที่ผ่านช่องทางบริการ
        </v-card-text>

        <v-card-actions>
          <v-btn
            color="red darken-1"
            text
            block
            @click="modal_failRegis = false"
          >
            ตกลง
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-row class="text-left">
      <v-col cols="12" md="7">
        <div class="px-4 mt-10">
          <div class="hidden-xs-only text_title">
            ร่วมเป็นส่วนหนึ่งกับ TaWai
            <div class="hidden-xs-only text_content mr-12">
              หนึ่งรายงานของท่าน คือ Big Data ของคนไทย
              ที่จะช่วยตัดสินใจก่อนเลือกใช้ผลิตภัณฑ์สุขภาพ
              <div align="center">
                <v-img
                  transition="scale-transition"
                  origin="center center"
                  src="http://www.tawainetwork.org/assetmaterials/regis2.png"
                  class="hidden-xs-only pt-8"
                  contain
                  max-height="400"
                  max-width="450"
                />
              </div>
            </div>
          </div>
        </div>
      </v-col>

      <v-col cols="12" md="5">
        <div class="pa-4">
          <v-row aglign="center" justify="left">
            <GoBack class="hidden-md-and-up mt-8 ml-2" />
            <v-col class="ml-2">
              <span class="text_question">สมัครสมาชิก</span>
              <br />
              <span class="text_content">สร้างบัญชี Tawai for Health</span>
            </v-col>
          </v-row>
          <div class="pt-4">
            <v-stepper class="elevation-0" v-model="e1">
              <template>
                <v-stepper-header class="elevation-0">
                  <template v-for="n in steps">
                    <v-stepper-step
                      :key="`${n}-step`"
                      :complete="e1 > n"
                      :step="n"
                    >
                      {{ stepName[n - 1] }}
                    </v-stepper-step>

                    <v-divider v-if="n !== steps" :key="n"></v-divider>
                  </template>
                </v-stepper-header>

                <v-stepper-items>
                  <v-stepper-content
                    v-for="n in steps"
                    :key="`${n}-content`"
                    :step="n"
                  >
                    <div v-if="n == 1">
                      <v-form
                        ref="form_step1"
                        v-model="valid_step1"
                        lazy-validation
                      >
                        <v-row>
                          <v-col cols="6" class="pb-0 pr-1">
                            <v-text-field
                              label="ชื่อจริง"
                              outlined
                              rounded
                              v-model="userRegis.fName"
                              required
                              :error-messages="fNameErrors"
                              @input="$v.userRegis.fName.$touch()"
                              @blur="$v.userRegis.fName.$touch()"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="6" class="pb-0 pl-1">
                            <v-text-field
                              label="นามสกุล"
                              outlined
                              rounded
                              v-model="userRegis.lName"
                              required
                              :error-messages="lNameErrors"
                              @input="$v.userRegis.lName.$touch()"
                              @blur="$v.userRegis.lName.$touch()"
                            ></v-text-field>
                          </v-col>
                        </v-row>

                        <v-text-field
                          label="อีเมลผู้ใช้งาน"
                          outlined
                          rounded
                          v-model="userRegis.email"
                          :error-messages="emailErrors"
                          required
                          @input="$v.userRegis.email.$touch()"
                          @blur="$v.userRegis.email.$touch()"
                        ></v-text-field>

                        <v-text-field
                          label="รหัสผ่าน"
                          outlined
                          rounded
                          :append-icon="hidePWD ? 'mdi-eye' : 'mdi-eye-off'"
                          :type="hidePWD ? 'text' : 'password'"
                          @click:append="hidePWD = !hidePWD"
                          v-model="userRegis.pwd"
                          :error-messages="pwdErrors"
                          required
                          @input="$v.userRegis.pwd.$touch()"
                          @blur="$v.userRegis.pwd.$touch()"
                        ></v-text-field>

                        <v-text-field
                          label="ยืนยันรหัสผ่าน"
                          outlined
                          rounded
                          :append-icon="hideTempPWD ? 'mdi-eye' : 'mdi-eye-off'"
                          :type="hideTempPWD ? 'text' : 'password'"
                          @click:append="hideTempPWD = !hideTempPWD"
                          v-model="tempPWD"
                          :error-messages="TempPwdErrors"
                          required
                          @input="$v.tempPWD.$touch()"
                          @blur="$v.tempPWD.$touch()"
                        ></v-text-field>

                        <v-btn
                          :disabled="!valid_step1"
                          rounded
                          large
                          class="mb-4"
                          width="100%"
                          color="primary"
                          @click="nextStep(n)"
                          >ถัดไป</v-btn
                        >
                      </v-form>
                    </div>

                    <div v-else-if="n == 2">
                      <v-form
                        ref="form_step2"
                        v-model="valid_step2"
                        lazy-validation
                      >
                        <v-text-field
                          class="pt-2"
                          label="ที่อยู่"
                          outlined
                          rounded
                          v-model="userRegis.address"
                          :error-messages="AddressErrors"
                          required
                          @input="$v.userRegis.address.$touch()"
                          @blur="$v.userRegis.address.$touch()"
                        ></v-text-field>

                        <v-row>
                          <v-col cols="6" class="py-0 pr-1">
                            <v-autocomplete
                              label=" เลือกจังหวัด"
                              item-text="arrProvince"
                              :items="arrProvince"
                              @click="selectProvince"
                              outlined
                              rounded
                              v-model="userRegis.province"
                              :error-messages="provErrors"
                              required
                              @input="$v.userRegis.province.$touch()"
                              @blur="$v.userRegis.province.$touch()"
                            ></v-autocomplete>
                          </v-col>
                          <v-col cols="6" class="py-0 pl-1">
                            <v-autocomplete
                              label="เลือกอำเภอ/เขต"
                              item-text="arrDistrict"
                              :items="arrDistrict"
                              @click="selectDistrict"
                              outlined
                              rounded
                              v-model="userRegis.district"
                              :error-messages="districtErrors"
                              required
                              @input="$v.userRegis.district.$touch()"
                              @blur="$v.userRegis.district.$touch()"
                            ></v-autocomplete>
                          </v-col>
                        </v-row>

                        <v-row>
                          <v-col cols="6" class="py-0 pr-1">
                            <v-autocomplete
                              label="เลือกตำบล/แขวง"
                              item-text="arrSubDistrict"
                              :items="arrSubDistrict"
                              @click="selectSubDistrict"
                              outlined
                              rounded
                              v-model="userRegis.subDistrict"
                              :error-messages="subDistrictErrors"
                              required
                              @input="$v.userRegis.subDistrict.$touch()"
                              @blur="$v.userRegis.subDistrict.$touch()"
                            ></v-autocomplete>
                          </v-col>
                          <v-col cols="6" class="py-0 pl-1">
                            <v-autocomplete
                              label="เลือกรหัสไปรษณีย์"
                              item-text="arrPostcode"
                              :items="arrPostcode"
                              outlined
                              rounded
                              v-model="userRegis.postcode"
                              :error-messages="postcodeErrors"
                              required
                              @input="$v.userRegis.postcode.$touch()"
                              @blur="$v.userRegis.postcode.$touch()"
                            ></v-autocomplete>
                          </v-col>
                        </v-row>

                        <v-row>
                          <v-col cols="6" class="py-0 pr-1">
                            <v-select
                              label="เลือกหน่วยงาน"
                              :items="arrOrg"
                              @click="cvToID_Org"
                              outlined
                              rounded
                              v-model="tempOrg"
                              :error-messages="orgIDErrors"
                              required
                              @input="$v.tempOrg.$touch()"
                              @blur="$v.tempOrg.$touch()"
                            ></v-select>
                          </v-col>
                          <v-col cols="6" class="py-0 pl-1">
                            <v-select
                              label="เลือกเขตพื้นที่"
                              :items="arrArea"
                              @click="cvToID_Area"
                              outlined
                              rounded
                              v-model="tempArea"
                              :error-messages="orgAreaErrors"
                              required
                              @input="$v.tempArea.$touch()"
                              @blur="$v.tempArea.$touch()"
                            ></v-select>
                          </v-col>
                        </v-row>

                        <v-row>
                          <v-col cols="6" class="py-0 pr-1">
                            <v-text-field
                              label="เบอร์โทรติดต่อประสานงาน"
                              v-mask="mask"
                              outlined
                              rounded
                              v-model="userRegis.telPers"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="6" class="py-0 pl-1">
                            <v-text-field
                              label="เบอร์โทรหน่วยงาน"
                              v-mask="mask"
                              outlined
                              rounded
                              v-model="userRegis.telOrg"
                            ></v-text-field>
                          </v-col>
                        </v-row>

                        <v-btn
                          :disabled="!valid_step2"
                          rounded
                          large
                          class="mb-4"
                          width="100%"
                          color="primary"
                          @click="click_Regis"
                          >สมัครสมาชิก</v-btn
                        >

                        <v-btn
                          :ripple="false"
                          :retain-focus-on-click="false"
                          rounded
                          large
                          class="mb-4"
                          width="100%"
                          text
                          @click="backStep(n)"
                          >ย้อนกลับ</v-btn
                        >
                      </v-form>
                    </div>
                  </v-stepper-content>
                </v-stepper-items>
              </template>
            </v-stepper>
          </div>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength, email } from "vuelidate/lib/validators";
import { mask } from "vue-the-mask";
import GoBack from "./actions/GoBack";

export default {
  mixins: [validationMixin],

  validations: {
    tempPWD: { required },
    tempOrg: { required },
    tempArea: { required },
    userRegis: {
      fName: { required },
      lName: { required },
      email: { required, email },
      pwd: { required, minLength: minLength(6) },
      address: { required },
      province: { required },
      district: { required },
      subDistrict: { required },
      postcode: { required }
    }
  },

  directives: {
    mask
  },
  components: {
    GoBack
  },

  data: () => ({
    e1: 1,
    steps: 2,
    modal_successRegis: false,
    modal_failRegis: false,
    hidePWD: false,
    hideTempPWD: false,
    valid_step1: false,
    valid_step2: false,
    tempPWD: "",
    tempOrg: "",
    tempArea: "",
    tempEmail: "",
    mask: "##-####-####",
    userRegis: {
      fName: "",
      lName: "",
      email: "",
      pwd: "",
      address: "",
      province: "",
      district: "",
      subDistrict: "",
      postcode: "",
      orgID: 0,
      orgArea: 0,
      telPers: "",
      telOrg: ""
    },
    stepName: ["ข้อมูลส่วนตัวผู้ใช้", "ข้อมูลองค์กรและที่อยู่"],
    arrProvinceID: [],
    arrDistrictID: [],
    arrSubDistrictID: [],
    arrProvince: [],
    arrDistrict: [],
    arrSubDistrict: [],
    arrPostcode: [],
    arrOrg: [],
    arrArea: []
  }),

  computed: {
    fNameErrors() {
      const errors = [];
      if (!this.$v.userRegis.fName.$dirty) return errors;
      !this.$v.userRegis.fName.required && errors.push("กรุณาระบุชื่อ");
      return errors;
    },
    lNameErrors() {
      const errors = [];
      if (!this.$v.userRegis.lName.$dirty) return errors;
      !this.$v.userRegis.lName.required && errors.push("กรุณาระบุนามสกุล");
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.userRegis.email.$dirty) return errors;
      !this.$v.userRegis.email.email && errors.push("รูปแบบของอีเมลไม่ถูกต้อง");
      !this.$v.userRegis.email.required &&
        errors.push("กรุณาระบุอีเมลผู้ใช้งาน");
      if (this.tempEmail == this.userRegis.email) {
        errors.push("อีเมลถูกใช้งานแล้ว");
        return errors;
      }
      return errors;
    },
    pwdErrors() {
      const errors = [];
      if (!this.$v.userRegis.pwd.$dirty) return errors;
      !this.$v.userRegis.pwd.minLength &&
        errors.push("กรุณาระบุรหัสผ่านอย่างน้อย 6 ตัวอักษร");
      !this.$v.userRegis.pwd.required && errors.push("กรุณาระบุรหัสผ่าน");
      return errors;
    },
    AddressErrors() {
      const errors = [];
      if (!this.$v.userRegis.address.$dirty) return errors;
      !this.$v.userRegis.address.required && errors.push("กรุณาระบุที่อยู่");
      return errors;
    },
    provErrors() {
      const errors = [];
      if (!this.$v.userRegis.province.$dirty) return errors;
      !this.$v.userRegis.province.required && errors.push("กรุณาระบุจังหวัด");
      return errors;
    },
    districtErrors() {
      const errors = [];
      if (!this.$v.userRegis.district.$dirty) return errors;
      !this.$v.userRegis.district.required && errors.push("กรุณาระบุอำเภอ/เขต");
      return errors;
    },
    subDistrictErrors() {
      const errors = [];
      if (!this.$v.userRegis.subDistrict.$dirty) return errors;
      !this.$v.userRegis.subDistrict.required &&
        errors.push("กรุณาระบุตำบล/แขวง");
      return errors;
    },
    postcodeErrors() {
      const errors = [];
      if (!this.$v.userRegis.postcode.$dirty) return errors;
      !this.$v.userRegis.postcode.required &&
        errors.push("กรุณาระบุรหัสไปรษณีย์");
      return errors;
    },
    orgIDErrors() {
      const errors = [];
      if (!this.$v.tempOrg.$dirty) return errors;
      !this.$v.tempOrg.required && errors.push("กรุณาระบุหน่วยงาน");
      return errors;
    },
    orgAreaErrors() {
      const errors = [];
      if (!this.$v.tempArea.$dirty) return errors;
      !this.$v.tempArea.required && errors.push("กรุณาระบุพื้นที่เขต");
      return errors;
    },
    TempPwdErrors() {
      const errors = [];
      if (!this.$v.tempPWD.$dirty) return errors;
      if (this.tempPWD != this.userRegis.pwd) {
        errors.push("รหัสผ่านไม่ตรงกัน");
        return errors;
      }
      !this.$v.tempPWD.required && errors.push("กรุณายืนยันรหัสผ่าน");
      return errors;
    },
    selectProvince() {
      if (this.userRegis.province.length > 0) {
        this.fetchDistrict();
      }
      return 0;
    },
    selectDistrict() {
      if (this.userRegis.district.length > 0) {
        this.fetchSubDistrict();
      }
      return 0;
    },
    selectSubDistrict() {
      if (this.userRegis.subDistrict.length > 0) {
        this.fetchPostcode();
      }
      return 0;
    },
    cvToID_Org() {
      if (this.tempOrg.length > 0) {
        this.confOrgID();
      }
      return 0;
    },
    cvToID_Area() {
      if (this.tempArea.length > 0) {
        this.confAreaID();
      }
      return 0;
    }
  },

  methods: {
    async nextStep(n) {
      if (n === this.steps) {
        //
      } else {
        this.$v.$touch();

        if (
          this.valid_step1 &&
          this.userRegis.fName.length > 0 &&
          this.userRegis.lName.length > 0 &&
          this.userRegis.email.length > 0 &&
          this.userRegis.pwd.length > 0
        ) {
          // Continue check email already use or not ?
          let validEmail = await this.checkEmail();
          // console.log('WTF', validEmail)
          if (validEmail) {
            this.e1 = n + 1;
          }
        }
      }
    },
    backStep(n) {
      if (n === this.steps) {
        this.e1 = 1;
      }
    },
    async click_Regis() {
      this.$v.$touch();
      if (this.valid_step2) {
        fetch("http://www.tawainetwork.org/webBackendPHP/register.php", {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            userRegis: this.userRegis
          })
        })
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            console.log("DATA.RES: ", dataRes);
            if (dataRes) {
              // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
              this.modal_successRegis = true;
            } else {
              this.modal_failRegis = true;
            }
          })
          .catch(err => {
            console.log("ERR.register: ", err);
          });
      }
    },
    fetchMountProvince() {
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_province.php",
        {
          method: "GET",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          }
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            this.arrProvince = JSON.parse(
              JSON.stringify(dataRes[1].map(prov => prov.province))
            );
            this.arrProvinceID = JSON.parse(
              JSON.stringify(dataRes[1].map(provID => provID.province_code))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchMountProvince: ", err);
        });
    },
    fetchMountOrg() {
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_fetchList.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            queryList_order: "departmentType"
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            this.arrOrg = JSON.parse(
              JSON.stringify(dataRes[1].map(label => label.CHOICE_LABEL))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchMountOrg: ", err);
        });
    },
    fetchMountArea() {
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_fetchList.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            queryList_order: "areaNumber"
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            this.arrArea = JSON.parse(
              JSON.stringify(dataRes[1].map(label => label.CHOICE_LABEL))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchMountOrg: ", err);
        });
    },
    fetchDistrict() {
      let provINDEX = this.arrProvince.findIndex(
        provINDEX => provINDEX === this.userRegis.province
      );
      // console.log('provID: ', this.arrProvinceID[provID])
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_amphoe.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            provinceCode: this.arrProvinceID[provINDEX]
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            this.arrDistrict = JSON.parse(
              JSON.stringify(dataRes[1].map(prov => prov.amphoe))
            );
            this.arrDistrictID = JSON.parse(
              JSON.stringify(dataRes[1].map(provID => provID.amphoe_code))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchAmphoe: ", err);
        });
    },
    fetchSubDistrict() {
      let districtINDEX = this.arrDistrict.findIndex(
        districtINDEX => districtINDEX === this.userRegis.district
      );
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_district.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            amphoeCode: this.arrDistrictID[districtINDEX]
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            this.arrSubDistrict = JSON.parse(
              JSON.stringify(dataRes[1].map(prov => prov.district))
            );
            this.arrSubDistrictID = JSON.parse(
              JSON.stringify(dataRes[1].map(provID => provID.district_code))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchSubDistrict: ", err);
        });
    },
    fetchPostcode() {
      let subDistrictINDEX = this.arrSubDistrict.findIndex(
        subDistrictINDEX => subDistrictINDEX === this.userRegis.subDistrict
      );
      fetch(
        "http://www.tawainetwork.org/mobilebackend/fetch_picker_postcode.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            districtCode: this.arrSubDistrictID[subDistrictINDEX]
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "true") {
            // console.log('PROVINCE: ', JSON.parse(JSON.stringify(dataRes[1])))
            let temp_postcode = JSON.parse(
              JSON.stringify(dataRes[1].map(postcode => postcode.zipcode))
            );
            if (temp_postcode.length < 2) {
              this.userRegis.postcode = temp_postcode[0];
            }
            this.arrPostcode = JSON.parse(
              JSON.stringify(dataRes[1].map(postcode => postcode.zipcode))
            );
          }
        })
        .catch(err => {
          console.log("ERR.fetchPostcode: ", err);
        });
    },
    confOrgID() {
      let orgINDEX = this.arrOrg.findIndex(label => label === this.tempOrg);
      this.userRegis.orgID = orgINDEX += 1;
    },
    confAreaID() {
      let areaINDEX = this.arrArea.findIndex(label => label === this.tempArea);
      this.userRegis.orgArea = areaINDEX += 1;
    },
    async checkEmail() {
      let existEmail = false;
      await fetch("http://www.tawainetwork.org/mobilebackend/checkEmail.php", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          email: this.userRegis.email
        })
      })
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes == "true") {
            // console.log('heyyyy')
            existEmail = true;
          } else {
            // console.log('nahhh')
            existEmail = false;
            this.tempEmail = this.userRegis.email;
          }
        })
        .catch(err => {
          console.log("ERR.fetchPostcode: ", err);
        });
      return existEmail;
    }
  },

  mounted() {
    this.fetchMountProvince();
    this.fetchMountOrg();
    this.fetchMountArea();
  }
};
</script>

